/*------------------------------------------------------------------------------
  Export Tools Tab
------------------------------------------------------------------------------*/
.status {
  background-color: #F4F4F4;
  padding: 12px 12px;
  margin: 20px -12px -23px;
  border-style: solid;
  border-width: 1px 0;
  border-color: #EAE9E9;
  overflow: auto;
  position: relative; }

.status .spinner {
  margin: 0 10px; }

.import-progress,
.export-progress {
  background: #DDDDDD;
  position: absolute;
  bottom: 15px;
  height: 15px;
  width: 95%; }

.import-progress div,
.export-progress div {
  height: 100%;
  background: #CCCCCC;
  width: 0; }

.admin-color-fresh .import-progress div,
.admin-color-fresh .export-progress div {
  background: #0073AA; }

.admin-color-light .import-progress div,
.admin-color-light .export-progress div {
