.admin-color-ectoplasm .export-progress div {
  background: #A3B745; }

.admin-color-midnight .import-progress div,
.admin-color-midnight .export-progress div {
  background: #E14D43; }

.admin-color-sunrise .import-progress div,
.admin-color-sunrise .export-progress div {
  background: #DD823B; }

